.container {
  grid-template: "header" 1.5fr
                 "search" 1fr
                 "content" 600px
                 "bottom"
                 / 1fr;
  display: grid;
}

.goHomeDiv {
  grid-area: bottom;
  width: fit-content;
  position: relative;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.goHomeBtn {
  background-color: #0000;
  border: 0 solid #0000;
}

.goHomeBtnIcon {
  block-size: 50px;
}

.searchBooksForm {
  grid-area: search;
  align-items: center;
  width: fit-content;
  margin-top: 20px;
  position: relative;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
}

.inputSearch {
  text-align: center;
  background-color: #fff;
  border: 0 solid #000;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  width: 200px;
  height: 44px;
  font-size: 20px;
}

.searchBtn {
  background-color: #fff;
  border: 0 solid #000;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  width: 90px;
  height: 44px;
  font-size: 20px;
  position: relative;
  top: 16px;
}

.searchBtnIcon {
  background-color: #0000;
  width: 90px;
  height: 44px;
}

.logoImg {
  grid-area: header;
  block-size: 70px;
  position: relative;
  top: 350px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.errorMessage_inputSearch {
  color: #f0f8ff;
  text-align: center;
  background-color: #0000;
  grid-area: search;
  font-family: Kanit, sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  position: relative;
  top: 0;
}

.bookSearchResults {
  background-color: #000;
  border: 4px solid #000;
  border-radius: 10px;
  grid-area: content;
  height: auto;
  margin-top: 10px;
  overflow: scroll;
}

.bookPreview {
  grid-template-rows: minmax(.7fr, 1fr) 1fr;
  background-color: #000;
  border: 2.5px solid #fb0;
  border-radius: 10px;
  grid-template-columns: 1fr 1.5fr 1.5fr;
  grid-template-areas: "bookPreviewImg bookPreviewTitle bookPreviewTitle"
                       "bookPreviewImg bookPreviewPages bookPreviewRating";
  width: auto;
  height: 134px;
  margin: 7px;
  display: grid;
  position: relative;
}

.bookPreview:hover {
  grid-template-rows: minmax(.7fr, 1fr) 1fr;
  background-color: #303030;
  border: 2.5px solid #fb0;
  border-radius: 10px;
  grid-template-columns: 1fr 1.5fr 1.5fr;
  grid-template-areas: "bookPreviewImg bookPreviewTitle bookPreviewTitle"
                       "bookPreviewImg bookPreviewPages bookPreviewRating";
  width: auto;
  height: 134px;
  margin: 7px;
  display: grid;
  position: relative;
}

.previewImgDiv, .previewImgDiv:hover {
  max-width: auto;
  max-height: auto;
  background-color: #0000;
  border-radius: 20px;
  grid-area: bookPreviewImg;
  position: relative;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

.overlay {
  backdrop-filter: blur(4px);
  z-index: 100;
  background-color: #0006;
  width: 100%;
  height: 100%;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.bookPreview__link {
  text-decoration: none;
}

.bookPreviewImg {
  border-radius: 5px;
  grid-area: bookPreviewImg;
  block-size: 110px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bookPreviewTitle {
  text-align: center;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  white-space: normal;
  color: #f0f8ff;
  background-color: #0000;
  -webkit-box-orient: vertical;
  grid-area: bookPreviewTitle;
  justify-content: center;
  max-width: 100%;
  max-height: 35px;
  margin: auto;
  font-size: 100%;
  display: -webkit-box;
  position: relative;
  overflow: hidden;
}

.bookPreviewPages {
  text-align: center;
  font-size: auto;
  color: #f0f8ff;
  background-color: #0000;
  grid-area: bookPreviewPages;
}

.bookPreviewRating {
  background-color: #0000;
  grid-area: bookPreviewRating;
  width: 110px;
  height: auto;
  position: absolute;
  top: 30%;
  left: 40%;
  transform: translate(-50%, -50%);
}

.bookPreviewRatingImg {
  fill: #0ff;
  background-color: #0000;
  grid-area: bookPreviewRating;
  block-size: 40px;
}

.bookPreviewRatingText {
  font-size: auto;
  color: #fff;
  background-color: #0000;
  grid-area: bookPreviewRating;
  position: absolute;
  top: 2px;
  left: 50px;
}

.bookInfo {
  max-height: auto;
  background-color: #000;
  grid-template: "goBackBtn goBackBtn" .3fr
                 "bookImg bookImg" 1fr
                 "bookTitle bookTitle"
                 "bookAuthor bookPages"
                 "bookDescr bookDescr"
                 / 2fr 1fr;
  width: fit-content;
  display: grid;
  position: absolute;
}

.goBackDiv {
  grid-area: goBackBtn;
}

.goBackBtnIcon {
  background-color: #0000;
  block-size: 50px;
}

.goBackBtn {
  color: #000;
  background-color: #0000;
  border: #000;
  position: absolute;
  top: 4px;
  left: 50%;
  transform: translateX(-50%);
}

.bookInfoImg {
  height: fit-content;
  border: 1px solid #0000;
  border-radius: 10px;
  block-size: 400px;
  margin-top: 63px;
  margin-bottom: 45px;
  transition: border-color .5s;
  animation: 7s infinite borderChange;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes borderChange {
  0% {
    border-color: #c59d53;
    box-shadow: 0 0 40px #c59d53;
  }

  25% {
    border-color: #fff;
    box-shadow: 0 0 40px #fff;
  }

  50% {
    border-color: #a726e2;
    box-shadow: 0 0 40px #a726e2;
  }

  75% {
    border-color: #ff0;
    box-shadow: 0 0 40px #ff0;
  }

  100% {
    border-color: #f90;
    box-shadow: 0 0 40px #f90;
  }
}

.bookInfoImg:hover {
  height: fit-content;
  border: 4px solid #bb830b;
  border-radius: 10px;
  block-size: 400px;
  margin-top: 63px;
  margin-bottom: 45px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.bookInfoImgDiv {
  grid-area: bookImg;
}

.bookTitleDiv {
  grid-area: bookTitle;
  height: fit-content;
}

.bookTitle {
  font-size: auto;
  text-align: center;
  color: #f7c900;
  margin: 40px 15px;
  font-family: Kanit, sans-serif;
  font-style: normal;
  font-weight: 200;
}

.bookAuthorDiv {
  background-color: #0000;
  grid-area: bookAuthor;
  position: relative;
}

.bookAuthor {
  color: #f0d9af;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: Kanit, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  position: relative;
  top: 30%;
  transform: translateY(-50%);
}

.bookPagesDiv {
  grid-area: bookPages;
}

.bookPages {
  color: #f0d9af;
  font-family: Kanit, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  font-size: auto;
  position: relative;
  top: 43%;
  left: 60%;
  transform: translate(-50%, -50%);
}

.bookDescriptionDiv {
  grid-area: bookDescr;
}

.descriptionTitle {
  text-align: center;
  color: #fff;
  padding-top: 35px;
  font-family: Kanit, sans-serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  position: relative;
}

.bookDescription {
  color: #fff;
  margin: 15px 20px 35px;
  font-family: Kanit, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
}

.hidden {
  display: none;
}

* {
  background-color: #000;
  margin: 0;
  padding: 0;
}

.container__home {
  background-image: url("f0787b7696f2a4c44d687065a2c13453.f06c652a.jpg");
  width: 100vw;
  min-height: 100vh;
}

@media screen and (width >= 340px) {
  .homeTitle {
    color: #f0f8ff;
    text-align: center;
    white-space: nowrap;
    text-shadow: -2px -2px #000, 2px -2px #000, -2px 2px #000, 2px 2px #000;
    background-color: #0000;
    font-family: Kanit, sans-serif;
    font-size: 1px;
    font-style: normal;
    font-weight: 600;
    transition-duration: .5s;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

