  }
}

@media screen and (width >= 409px) {
  .homeTitle {
    color: #f0f8ff;
    text-align: center;
    white-space: nowrap;
    text-shadow: -2px -2px #000, 2px -2px #000, -2px 2px #000, 2px 2px #000;
    background-color: #0000;
    font-family: Kanit, sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    transition-duration: .5s;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.homeBtnStart {
  color: #000;
  block-size: 40px;
  background-color: #f5ec70;
  border: 2px solid #b90;
  border-radius: 20px;
  width: 100px;
  height: 40px;
  padding-bottom: 40px;
  font-family: Playfair Display, serif;
  font-size: 26px;
  font-weight: 600;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
